/* Modern About Component Styles */
.About {
  background-color: white;
  border-radius: 12px;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
  padding: 2rem;
  max-width: 500px;
  width: 100%;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.About:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.15);
}

.About-header {
  text-align: left;
  font-family: 'Inter', sans-serif;
  height: auto;
  padding: 0;
}

.About-title {
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 1.5rem;
  position: relative;
  display: inline-block;
  color: #111827;
}

.About-title::after {
  content: '';
  position: absolute;
  width: 50px;
  height: 3px;
  background: linear-gradient(90deg, #0066FF, #8B5CF6);
  bottom: -10px;
  left: 0;
  border-radius: 2px;
}

.About-intro {
  font-family: 'Inter', sans-serif;
  font-size: 1rem;
  line-height: 1.6;
  color: #374151;
}

@media (max-width: 768px) {
  .About {
    max-width: 100%;
  }
}
