/* Modern Chatbot Component Styles */
.chatbot-container {
  display: flex;
  flex-direction: column;
  height: 500px;
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
  background-color: white;
  border-radius: 12px;
  box-shadow: 0 6px 24px rgba(0, 0, 0, 0.15);
  overflow: hidden;
}

.chatbot-header {
  padding: 16px 20px;
  background-color: #f8f9fa;
  border-bottom: 1px solid #e5e7eb;
}

.chatbot-title {
  display: flex;
  align-items: center;
  gap: 10px;
  font-weight: 600;
  color: #111827;
}

.chatbot-title svg {
  color: #0066FF;
}

.chatbot-messages {
  flex: 1;
  padding: 20px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 16px;
  scroll-behavior: smooth;
  background-color: #f9fafc;
}

.message {
  display: flex;
  align-items: flex-start;
  max-width: 80%;
}

.message.user {
  margin-left: auto;
  justify-content: flex-end;
}

.message.bot {
  margin-right: auto;
}

.avatar {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background-color: #edf2ff;
  margin-right: 12px;
  color: #0066FF;
}

.message-bubble {
  padding: 12px 16px;
  border-radius: 18px;
  background-color: white;
  color: #111827;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
  border: 1px solid #e5e7eb;
  line-height: 1.5;
}

.message.user .message-bubble {
  background-color: #0066FF;
  color: white;
  border: none;
}

.chatbot-input {
  display: flex;
  align-items: center;
  padding: 16px 20px;
  background-color: white;
  border-top: 1px solid #e5e7eb;
}

.chat-input {
  flex: 1;
  padding: 10px 16px;
  border-radius: 24px;
  border: 1px solid #e5e7eb;
  background-color: #f3f4f6;
  font-size: 14px;
  transition: all 0.2s ease;
}

.chat-input:focus {
  outline: none;
  border-color: #0066FF;
  background-color: white;
  box-shadow: 0 0 0 2px rgba(0, 102, 255, 0.1);
}

.chat-send-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 36px;
  height: 36px;
  margin-left: 10px;
  border-radius: 50%;
  background-color: #0066FF;
  color: white;
  border: none;
  cursor: pointer;
  transition: all 0.2s ease;
}

.chat-send-button:hover {
  background-color: #0055e8;
  transform: scale(1.05);
}

.chat-send-button:disabled {
  background-color: #a8a8a8;
  cursor: not-allowed;
  transform: none;
}

.chatbot-footer {
  padding: 10px;
  background-color: #f8f9fa;
  border-top: 1px solid #e5e7eb;
  font-size: 12px;
  color: #6b7280;
  text-align: center;
}

/* Typing indicator */
.typing-indicator {
  display: flex;
  align-items: center;
  background-color: #e5e7eb;
  padding: 8px 16px;
  border-radius: 18px;
}

.typing-indicator span {
  height: 8px;
  width: 8px;
  background-color: #666;
  border-radius: 50%;
  margin: 0 2px;
  display: inline-block;
  opacity: 0.6;
  animation: dot-pulse 1.5s infinite ease-in-out;
}

.typing-indicator span:nth-child(2) {
  animation-delay: 0.2s;
}

.typing-indicator span:nth-child(3) {
  animation-delay: 0.4s;
}

@keyframes dot-pulse {
  0%, 100% { 
    transform: scale(0.8);
    opacity: 0.6;
  }
  50% { 
    transform: scale(1.2);
    opacity: 1;
  }
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .chatbot-container {
    height: 400px;
    width: 100%;
  }
  
  .message {
    max-width: 90%;
  }
}

@media (max-width: 480px) {
  .chatbot-container {
    border-radius: 0;
    height: 450px;
  }
  
  .message {
    max-width: 95%;
  }
  
  .chatbot-header,
  .chatbot-input {
    padding: 12px 16px;
  }
}